import userInfoModal from "@/components/userInfoModal";
export default {
  name: "index",
  components: {
    userInfoModal
  },
  data() {
    return {
      //swiper3
      swiperOption: {
        autoplay: true, //自动轮播间隔
        speed: 1000, // 轮播速度
        loop: true, //开启轮播图前后循环模式
        // 设置轮播点,并且允许点击切换
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        },
        autoplay: {
          disableOnInteraction: false
        }
      },
      swiperOptionNews: {
        autoplay: true, //自动轮播间隔
        speed: 1000, // 轮播速度
        loop: true, //开启轮播图前后循环模式
        direction: "vertical",
        autoHeight: true
      },
      swiperOptionRanking: {
        speed: 1000, // 轮播速度
        navigation: {
          nextEl: ".ranking_number", //前进按钮的css选择器或HTML元素。
          prevEl: ".ranking_money" //后退按钮的css选择器或HTML元素。
        }
      },
      swiperOptionMongolia: {
        autoplay: true, //自动轮播间隔
        speed: 1000, // 轮播速度
        loop: true, //开启轮播图前后循环模式
        autoHeight: true,
        autoplay: {
          disableOnInteraction: false
        }
      },

      rankingList: [
        { name: "奖学金排行榜", class: "ranking_money", type: true },
        { name: "销量排行榜", class: "ranking_number", type: false }
      ],

      option: {
        IsMongolia: null,
        index: 0,
        IsBindPhone: false,
        IsUserModal: false
      }
    };
  },
  computed: {},
  mounted() {
    this.firstOpenHtml();
    this.setBodyEle();
  },
  methods: {
    selectRanking: function(item, index) {
      const _this = this;
      _this.rankingList.map(function(event, idx) {
        if (event.name == item.name) {
          event.type = true;
          _this.$set(_this.rankingList, idx, event);
        } else {
          event.type = false;
          _this.$set(_this.rankingList, idx, event);
        }
      });
    },
    remCustomModal: function(name) {
      if (name == "IsUserModal") {
        this.option.IsUserModal = false;
      }
      if (name == "IsMongolia") {
        this.option.IsMongolia = false;
      }
      this.setBodyEle();
    },
    setBodyEle: function() {
      let _body = document.querySelector("body");
      let _html = document.querySelector("html");
      if (
        this.option.IsMongolia ||
        !this.option.IsBindPhone ||
        this.option.IsUserModal
      ) {
        _body.style.overflow = "hidden";
        _html.style.overflow = "hidden";
        _html.style.height = 100 + "%";
        _body.style.height = 100 + "%";
      } else {
        _body.style.overflow = "auto";
        _html.style.overflow = "auto";
        _html.style.height = "auto";
        _body.style.height = "auto";
      }
    },
    firstOpenHtml: function() {
      let firstOpenHtml = sessionStorage.getItem("firstOpenHtml");
      this.option.index = firstOpenHtml || this.option.index;
      this.option.index++;
      sessionStorage.setItem("firstOpenHtml", this.option.index);
      this.isFirstOpenHtml();
    },
    isFirstOpenHtml: function() {
      let firstOpenHtml = sessionStorage.getItem("firstOpenHtml");
      if (firstOpenHtml == 1) {
        this.option.IsMongolia = true;
      } else {
        this.option.IsMongolia = false;
      }
    },
    bindPhone: function() {
      this.option.IsBindPhone = true;
      this.setBodyEle();
    },
    openUserInfo: function() {
      this.option.IsUserModal = true;
      this.setBodyEle();
    }
  }
};



